





























import Vue from 'vue'
import { getUserInfo } from '@/services/user'
export default Vue.extend({
  data() {
    return {
      userInfo: {}
    }
  },
  created() {
    this.loadUserInfo()
    this.loadUserInfo()
  },
  methods: {
    async loadUserInfo() {
      const { data } = await getUserInfo()
      if (data.state) {
        this.userInfo = data.content
      }
    },
    loginout() {
      this.$confirm('确定要退出吗？', '提示')
        .then(() => {
          this.$store.commit('setUser', null)
          this.$router.push({
            name: 'Login'
          })
          this.$message({
            type: 'success',
            message: '退出成功'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消退出'
          })
        })
    }
  }
})
